import React, { Fragment } from 'react';
import logo from './logo/3.png';

const Navbar = ({ lang, switchLang }) => {
  return (
    <Fragment>
      <div className='navbar-fixed'>
        <nav>
          <div className='nav-wrapper hoverable blue darken-2'>
            <a href='#home' className='brand-logo'>
              <img className='hoverable logo' alt='' src={logo} />
            </a>
            <a href='#' data-target='mobile-demo' className='sidenav-trigger'>
              <i className='material-icons'>menu</i>
            </a>
            <ul id='nav-mobile' className='right hide-on-med-and-down'>
              <li>
                <a href='#home'>{lang === 'en' ? 'Home' : 'Домой'}</a>
              </li>
              <li>
                <a href='#projects'>{lang === 'en' ? 'Projects' : 'Работы'}</a>
              </li>
              <li>
                <a
                  href='http://github.com/yalalex/'
                  rel='noopener noreferrer'
                  target='_blank'
                >
                  Github
                </a>
              </li>
            </ul>
            <ul id='nav-mobile' className='right'>
              <li>
                <a onClick={switchLang}>{lang === 'en' ? 'RU' : 'EN'}</a>
              </li>
            </ul>
          </div>
        </nav>
      </div>
      <ul className='sidenav' id='mobile-demo'>
        <li>
          <a href='#home'>{lang === 'en' ? 'Home' : 'Домой'}</a>
        </li>
        <li>
          <a href='#projects'>{lang === 'en' ? 'Projects' : 'Работы'}</a>
        </li>
        <li>
          <a
            href='http://github.com/yalalex/'
            rel='noopener noreferrer'
            target='_blank'
          >
            Github
          </a>
        </li>
      </ul>
    </Fragment>
  );
};

export default Navbar;
