import React from 'react';
import fid1 from './screenshots/fid1.PNG';
import fid2 from './screenshots/fid2.PNG';
import pf1 from './screenshots/pf1.PNG';
import pf2 from './screenshots/pf2.PNG';
import wa1 from './screenshots/wa1.PNG';
import wa2 from './screenshots/wa2.PNG';

const Projects = ({ lang }) => {
  return (
    <div id='projects' style={{ paddingTop: '60px' }}>
      <div className='row grey-text text-darken-2'>
        <h4 className='header'>Pet Finder</h4>
        <div className='card'>
          <div className='card horizontal'>
            <div className='card-image'>
              <img
                className='materialboxed responsive-img'
                src={pf1}
                alt=''
                width='500'
              />
            </div>
            <div className='card-image'>
              <img
                className='materialboxed responsive-img'
                src={pf2}
                alt=''
                width='500'
              />
            </div>
          </div>
          <div className='card-content'>
            {lang === 'en' ? (
              <div>
                <p>
                  Full stack (MERN) web application for advertising lost/found
                  pets in Belarus.
                </p>
                <p>Front end: React.js, Redux.js.</p>
                <p>Back end: Node.js, Express.js.</p>
                <p>Database: MongoDB.</p>
                <p>API's: Yandex Maps, LocationIQ.</p>
              </div>
            ) : (
              <div>
                <p>
                  Фулл-стек (MERN) веб-приложение для размещения объявлений о
                  пропаже/находке домашних животных в Беларуси.
                </p>
                <p>Front end: React.js, Redux.js.</p>
                <p>Back end: Node.js, Express.js.</p>
                <p>Database: MongoDB.</p>
                <p>API's: Yandex Maps, LocationIQ.</p>
              </div>
            )}
          </div>
          <div className='card-action'>
            <a
              rel='noopener noreferrer'
              target='_blank'
              href='http://pet-finder-by.herokuapp.com'
            >
              {lang === 'en' ? 'Website' : 'Веб-сайт'}
            </a>
            <a
              href='https://github.com/yalalex/pet-finder'
              rel='noopener noreferrer'
              target='_blank'
            >
              Github
            </a>
          </div>
        </div>
      </div>

      <div className='row grey-text text-darken-2'>
        <h4 className='header'>Weather App</h4>
        <div className='card'>
          <div className='card horizontal'>
            <div className='card-image'>
              <img
                className='materialboxed responsive-img'
                src={wa1}
                alt=''
                width='500'
              />
            </div>
            <div className='card-image'>
              <img
                className='materialboxed responsive-img'
                src={wa2}
                alt=''
                width='500'
              />
            </div>
          </div>
          <div className='card-content'>
            {lang === 'en' ? (
              <div>
                <p>
                  Web application for searching weather in more than 134,000
                  cities in the world.
                </p>
                <p>
                  Provides information on current weather, as well as a detailed
                  forecast for the next 48 hours and a forecast for 15 days.
                </p>
                <p>Made with React.js + Redux.js.</p>
                <p>API's: GeoDB Cities, OpenWeatherMap, WeatherBit.</p>
              </div>
            ) : (
              <div>
                <p>
                  Веб-приложение для поиска погоды в более чем 134 000 городах
                  мира.
                </p>
                <p>
                  Предоставляет информацию по текущей погоде, а так же подробный
                  прогноз на ближайшие 48 часов и прогноз на 15 дней.
                </p>
                <p>Сделано на React.js + Redux.js.</p>
                <p>
                  Используемые API: GeoDB Cities, OpenWeatherMap, WeatherBit.
                </p>
              </div>
            )}
          </div>
          <div className='card-action'>
            <a
              rel='noopener noreferrer'
              target='_blank'
              href='/weather-app-redux'
            >
              {lang === 'en' ? 'Website' : 'Веб-сайт'}
            </a>
            <a
              href='https://github.com/yalalex/weather-app-redux'
              rel='noopener noreferrer'
              target='_blank'
            >
              Github
            </a>
          </div>
        </div>
      </div>

      <div className='row grey-text text-darken-2'>
        <h4 className='header'>Face Info Detector</h4>
        <div className='card'>
          <div className='card horizontal'>
            <div className='card-image'>
              <img
                className='materialboxed responsive-img'
                src={fid1}
                alt=''
                width='500'
              />
            </div>
            <div className='card-image'>
              <img
                className='materialboxed responsive-img'
                src={fid2}
                alt=''
                width='500'
              />
            </div>
          </div>
          <div className='card-content'>
            {lang === 'en' ? (
              <div>
                <p>
                  Web application for detecting persons' gender, age and race on
                  the URL based photo.
                </p>
                <p>Front-End: React.js.</p>
                <p>
                  Back-End: Node.js (Express.js) + PostgreSQL + Clarifai API.
                </p>
              </div>
            ) : (
              <div>
                <p>
                  Веб-приложение, определяющее пол, возраст и расовую
                  принадлежность людей на фотографии, предоставленной
                  посредством URL-адреса.
                </p>
                <p>Front-End: React.js.</p>
                <p>
                  Back-End: Node.js (Express.js) + PostgreSQL + Clarifai API.
                </p>
              </div>
            )}
          </div>
          <div className='card-action'>
            <a
              rel='noopener noreferrer'
              target='_blank'
              href='/face-info-detector'
            >
              {lang === 'en' ? 'Website' : 'Веб-сайт'}
            </a>
            <a
              href='https://github.com/yalalex/face-info-detector'
              rel='noopener noreferrer'
              target='_blank'
            >
              Github
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Projects;
