import React, { Fragment } from 'react';

const Contact = () => {
  return (
    <Fragment>
      <div className='fixed-action-btn'>
        <a className='btn-floating btn-large grey darken-1'>
          <i className='large material-icons'>mail</i>
        </a>
        <ul>
          <li>
            <a
              href='https://t.me/alex_yal'
              className='btn-floating blue lighten-1'
              rel='noopener noreferrer'
              target='_blank'
            >
              <i className='material-icons telegram'>send</i>
            </a>
          </li>
          <li>
            <a href='mailto:yalalex.dev@gmail.com' className='btn-floating'>
              <i className='material-icons'>mail_outline</i>
            </a>
          </li>
        </ul>
      </div>
    </Fragment>
  );
};

export default Contact;
